import axios from 'axios'
import { Toast } from 'vant'
import store from '@/store'
// 创建axios实例，将来对创建出来的实例进行自定义配置
// 好处：不会污染原始的axios实例
const instance = axios.create({
  baseURL: 'https://smart-shop.itheima.net/index.php?s=/api/',
  timeout: 5000
})

// 自定义配置 请求/响应 拦截器
// 添加请求拦截器
instance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // 开启loading,禁止背景点击(节流处理，防止多次点击)
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    loadingType: 'spinner', // 配置loading图标
    duration: 0 // 配置loading时长(0表示一直显示)
  })

  // 只要有token就在请求头中携带，便于请求需要token的接口
  const token = store.getters.token // store.getters.token ???
  if (token) {
    config.headers['Access-Token'] = token
    config.headers.platform = 'H5'
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么 (默认axios中返回的就是response.data，需要响应拦截器来返回response.data)
  const res = response.data
  if (res.status !== 200) {
    // 提示错误信息(this.$toast不是组件环境不能使用)
    // Toast是单例对象，后面再调用Toast，会将前面的Toast覆盖，同时只能显示一个Toast
    Toast(res.message)
    return Promise.reject(res.message)
  } else {
    // 关闭loading
    Toast.clear()
  }
  return res
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  return Promise.reject(error)
})

// 导出axios实例
export default instance
