import { getInfo, setInfo } from '@/utils/storage'

// 存储个人权证信息给vuex
export default {
  namespaced: true,
  state () {
    return {
      // 个人权证信息
      userInfo: getInfo()

    }
  },
  mutations: {
    // 存储个人权证信息
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      // 清空个人权证信息
      context.commit('setUserInfo', {})

      // 购物车信息重置（跨模块调用mutition）
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {}
}
